@import url(https://fonts.googleapis.com/css2?family=Schoolbell&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  white-space: pre-line;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

button {
  background-color: transparent;
  color: rgba(255, 255, 255, 0.8);
  border: 0;
  display: inline-block;
  width: 100px;
}

.my-collapsible {
  background-color: gold;
  align-items: center;
  text-align: center;
}

.welcome {
  position: relative;
  top: 50%;
  width: 65vw;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-left: auto;
  margin-right: auto;
}

.my-collapsible__content {
  overflow: hidden;
  background: gold;
}

.my-collapsible__content-inner {
  padding: 1rem;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.my-collapsible__content-inner h1{
  font-family: 'Schoolbell', cursive;
  font-size: 50px;
}

.number {
  -webkit-font-feature-settings: "tnum";
          font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;
  min-width: 3em;
  display: inline-block;
  text-align: left;
}

.timer__content {
  background: gold;
  height: 15vh;
  min-height: 100px;
  width: 100vw;
  display: -webkit-box;
  -webkit-box-pack: justify;
  -webkit-box-align: center;
  display: -moz-box;
  -moz-box-pack: justify;
  -moz-box-align: center;
  display: -ms-flexbox;
  -ms-flex-pack: justify;
  -ms-flex-align: center;
  display: box;
  box-pack: justify;
  box-align: center;
}

.inline-block-child h1{
  font-family: 'Schoolbell', cursive;
  font-size: 25px;
}

canvas {
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);               
}

.game-container {
  height: 85vh;
}

.game-container-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
}

.game-container-inner h1{
  font-family: 'Schoolbell', cursive;
  font-size: 20px;
}

.canvas-container {
  height: 400px;
}

.button1{
  display:inline-block;
  padding:0.35em 1.2em;
  border:0.1em solid gold;
  margin:0 0.3em 0.3em 0;
  border-radius:0.12em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:gold;
  text-align:center;
  transition: all 0.2s;
  -webkit-animation: bouncy 5s infinite linear;
          animation: bouncy 5s infinite linear;
  position: relative;
  }
  @-webkit-keyframes bouncy {
    0%{top:0em}
    40%{top:0em}
    43%{top:-0.9em}
    46%{top:0em}
    48%{top:-0.4em}
    50%{top:0em}
    100%{top:0em;}
  }
  @keyframes bouncy {
    0%{top:0em}
    40%{top:0em}
    43%{top:-0.9em}
    46%{top:0em}
    48%{top:-0.4em}
    50%{top:0em}
    100%{top:0em;}
  }
  .button1:hover{
  color:white;
  background-color:gold;
  }
  @media all and (max-width:30em){
   .button1{
  display:block;
  margin:0.4em auto;
  }
}


